import React from 'react';

class AddCompany extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            company:{
                name: '',
                code: '',
                address: '',
                zipcode: '',
                city: '',
                function: '',
                interlocutor_name: '',
                interlocutor_password: '',
                interlocutor_function: '',
                interlocutor_phone: '',
                interlocutor_email: '',
            },
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(fieldName) {
        return ((event) => {
            let company = this.state.company;
            company[fieldName] = event.target.value;
            this.setState({company: company});
        } );
    }

    handleSubmit(event) {
        console.log('A company was submitted: ' + JSON.stringify(this.state.company));
        event.preventDefault();

        // prepare data for edit profile API
        let myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        myHeaders.append("x-access-token", sessionStorage.getItem('token'));

        let form = new FormData(document.getElementById('add_company_form'));

        // Send form to submit app API and get response
        fetch('http://vps432643.ovh.net:3001/api/company/new', {
            method: 'POST',
            headers: myHeaders,
            body: form,
        })
            .then(function (response) {
                return response.json();
            })
            .then((jsonData) => {
                console.log("/api/company/new' POST then", jsonData);
                this.props.notify('Entreprise ajoute avec succs','info');
                this.props.refreshCompaniesList();
            })
            .catch(err => {
                // can't get response (server is down, ....) ==> show error message to user (unable ...)
                this.props.notify('Operation termin avec erreur ','error');
                console.log("/api/company/new' POST catch", err);
            });
    }

    handleSubmitTestUpdate(event) {
        //console.log('A company was submitted: ' + JSON.stringify(this.state.company));
        event.preventDefault();

        // prepare data for edit profile API
        let myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        myHeaders.append("x-access-token", sessionStorage.getItem('token'));

        let form = new FormData(document.getElementById('add_company_form'));
        // Send form to submit app API and get response
        fetch('http://vps432643.ovh.net:3001/api/company/update/5aaa4833bc09ff1bc46fea62', {
            method: 'PUT',
            headers: myHeaders,
            body: form,
        })
            .then(function (response) {
                return response.json();
            })
            .then((jsonData) => {
                console.log("/api/company/update/5aaa4833bc09ff1bc46fea62", jsonData);
                this.props.refreshCompaniesList();
            })
            .catch(err => {
                // can't get response (server is down, ....) ==> show error message to user (unable ...)
                console.log("/api/company/update/5aaa4833bc09ff1bc46fea62", err);
            });
    }

    render(){
        return (
            <div className="card">
                <div className="card-body">
                    <button onClick={this.props.hideAddCompanyForm} type="button" className="btn btn-default btn-circle btn-remove"><i className="fa fa-remove"/></button>
                    <form id="add_company_form" className="row" onSubmit={this.handleSubmit}>
                        <div className="col-md-6">
                            <h5 className="card-title"> Entreprises </h5>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Code</label>
                                <div className="col-sm-8">
                                    <input required name="code" type="text" className="form-control" value={this.state.company.code} onChange={this.handleChange('code')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Socit</label>
                                <div className="col-sm-8">
                                    <input required name="name" type="text" className="form-control" value={this.state.company.name} onChange={this.handleChange('name')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Addresse</label>
                                <div className="col-sm-8">
                                    <input name="address" type="text" className="form-control" value={this.state.company.address} onChange={this.handleChange('address')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Code Postal</label>
                                <div className="col-sm-8">
                                    <input name="zipcode" type="number" className="form-control" value={this.state.company.zipcode} onChange={this.handleChange('zipcode')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Ville</label>
                                <div className="col-sm-8">
                                    <input name="city" type="text" className="form-control" value={this.state.company.city} onChange={this.handleChange('city')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Fonction</label>
                                <div className="col-sm-8">
                                    <input name="function" type="text" className="form-control" value={this.state.company.function} onChange={this.handleChange('function')} />
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <h5 className="card-title"> Interloculeur </h5>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Nom</label>
                                <div className="col-sm-8">
                                    <input required name="interlocutor_name" type="text" className="form-control" value={this.state.company.interlocutor_name} onChange={this.handleChange('interlocutor_name')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Mot de passe</label>
                                <div className="col-sm-8">
                                    <input required name="interlocutor_password" type="text" className="form-control" value={this.state.interlocutor_password} onChange={this.handleChange('interlocutor_password')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Fonction</label>
                                <div className="col-sm-8">
                                    <input name="interlocutor_function" type="text" className="form-control" value={this.state.company.interlocutor_function} onChange={this.handleChange('interlocutor_function')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Tl/Fax</label>
                                <div className="col-sm-8">
                                    <input name="interlocutor_phone" type="number" className="form-control" value={this.state.company.interlocutor_phone} onChange={this.handleChange('interlocutor_phone')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Email</label>
                                <div className="col-sm-8">
                                    <input name="interlocutor_email" type="email" className="form-control" value={this.state.company.interlocutor_email} onChange={this.handleChange('interlocutor_email')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <div className="col-sm-12">
                                    <button type="submit" className="btn btn-primary">Appliquer</button>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default AddCompany;


