import React                            from 'react';
import Header                           from './components/Header';
import Footer                           from './components/Footer';

import './App.css';
import { ToastContainer, toast } from 'react-toastify';
import Companies from "./components/companies/Companies";

class App extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            connected: false,
            user: null,
            token: ""
        };
        this.onLogin = this.onLogin.bind(this);
        this.onLogout = this.onLogout.bind(this);
        this.notify = this.notify.bind(this);
    }

    onLogin(user,token){
        console.log("*************  onLogin  **************");
        this.setState({
            user: user,
            token: token,
            connected: true
        });
        sessionStorage.setItem('token', this.state.token);
        sessionStorage.setItem('user', JSON.stringify(this.state.user));
    }

    onLogout(){
        console.log("*************  onLogout  **************");
        this.setState({
            user: null,
            token: "",
            connected: false
        });
        sessionStorage.removeItem('user');
        sessionStorage.removeItem('token');
    }

    notify(msg,type){
        let msgPosition = {position: toast.POSITION.TOP_RIGHT}
        switch(type) {
            case 'success':
                toast.success(msg,msgPosition); break;
            case 'error':
                toast.error(msg,msgPosition); break;
            case 'warn':
                toast.warn(msg,msgPosition); break;
            case 'info':
                toast.info(msg,msgPosition); break;
            default : toast(msg,msgPosition);
        }
    };

    render() {
        return (
             <div>
                 <Header/>
                 <ToastContainer autoClose={5000} hideProgressBar={true} />
                 <Companies notify={this.notify} />
                 <Footer/>
            </div>
        );
    }
}

export default App;



