import React from 'react';
import AddCompany from './AddCompany';
import CompanyCell from './CompanyCell';
import Loader from '../common/Loader';
import Error from '../common/Error';

class Companies extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            companies: [],
            status: "loading",
            showAddCompanyForm: false,
        };
        this.getCompaniesList = this.getCompaniesList.bind(this);
        this.callApiGetCompaniesList = this.callApiGetCompaniesList.bind(this);
    }

    componentDidMount(){
        this.getCompaniesList();
    }

    getCompaniesList(){
        this.callApiGetCompaniesList((success,type,msg)=>{
            if (!success) {
                this.props.notify( msg ,type);
                this.setState({
                    status: "error",
                });

            }else{
                this.setState({
                    status: "success"
                });
            }
        });
    }

    callApiGetCompaniesList(callback){

        // prepare data for get profile API
        let myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        myHeaders.append("x-access-token", sessionStorage.getItem('token'));
/*
        let username = sessionStorage.getItem('user') ? JSON.parse(sessionStorage.getItem('user') ).username : null ;
        if (!username){
            callback(false,'error',"No user connected !!");
            return;
        }
*/
        // call get profile API and get response
        fetch(`http://vps432643.ovh.net:3001/api/company/list`, {
            method: 'GET',
            headers: myHeaders,
        })
            .then(function(response) {
                return response.json();
            })
            .then((jsonData)=> {
                this.setState({companies: jsonData});
                callback(true);
                console.log('>>>>  list of companies THEN', jsonData);
            })
            .catch( err => {
                // can't get response (server is down, ....) ==> show error message to user (unable ...)
                callback(false,'error',"Service down, please wait for re-submit ==> " + err);
            });

    }

    render(){

        switch(this.state.status) {
            case 'loading':
                return ( <Loader/>);
            case 'error':
                return ( <Error/>);
            default :
            let content = this.state.showAddCompanyForm ? <AddCompany notify={this.props.notify} refreshCompaniesList={this.getCompaniesList} hideAddCompanyForm={()=>{this.setState({showAddCompanyForm: false})}} /> : null;

            let companies = this.state.companies.map((company, index)=> {
                return (
                    <CompanyCell key={index} company={company}/>
                )
            });
            return (
                <div className="card">
                    <div className="card-body">
                        <h5 className="card-title">
                            Entreprises
                            <button onClick={()=>{this.setState({showAddCompanyForm: true})}} type="button" className="btn btn-default btn-circle"><i className="fa fa-plus"/></button>
                        </h5>
                        {content}
                        <br/>
                        <table className="table table-hover">
                            <thead className="thead-light">
                                <tr>
                                    <th scope="col">Socit</th>
                                    <th scope="col">Code</th>
                                    <th scope="col">Addresse</th>
                                    <th scope="col">CP</th>
                                    <th scope="col">Ville</th>
                                </tr>
                            </thead>
                            {companies}

                        </table>
                    </div>
                </div>
            )
        }
    }
}

export default Companies;


