import React from 'react';

class CompanyCell extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            company: this.props.company
        };
    }

    componentWillReceiveProps(nextProps) {
        this.setState({ company: nextProps.company });
    }

    render(){

        let company = this.state.company;
        return (
            <tbody>
            <tr className="clickable" data-toggle="collapse" data-target={"#"+company.code} aria-expanded="false" aria-controls={company.code} >
                <th scope="row">{company.name}</th>
                <td>{company.code}</td>
                <td>{company.address}</td>
                <td>{company.zipcode}</td>
                <td>{company.city}</td>
            </tr>
            <tr id={company.code} className="collapse" >
                <td> Interlocuteur </td>
                <td> {company.interlocutor_name}</td>
                <td> {company.interlocutor_email}</td>
                <td> {company.interlocutor_phone}</td>
                <td> {company.interlocutor_function}</td>
            </tr>
            </tbody>
        )
    }
}

export default CompanyCell;


