import React from 'react';

export default class Error extends React.Component {

    render(){
        return (
            <div className="text-center">
                <i className="fa fa-exclamation-circle" style={{fontSize: '80px'}}/>
                <h2>Server Error Occurred ... Sorry</h2>
            </div>
        );
    }
}


